body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 20px;
  line-height: 1.6;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

/* Navigation Styles */
.nav {
  background-color: #f8f9fa;
  padding: 1rem;
  margin-bottom: 2rem;
}

.nav a {
  margin-right: 1rem;
  text-decoration: none;
  color: #333;
}

.nav a:hover {
  color: #007bff;
}

/* Cookie Settings Styles */
.cookie-section {
  background-color: #f8f9fa;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 4px;
}

.cookie-toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
}

.button:hover {
  background-color: #0056b3;
}

/* Common Styles */
h1 {
  margin-bottom: 1.5rem;
}

h2 {
  margin-top: 2rem;
}

.additional-info {
  margin-top: 2rem;
}